import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterLink } from '@angular/router';
import { NgChartsModule } from 'ng2-charts';
import { ChartConfiguration, ChartData } from 'chart.js';
import { AuthService } from '../../../core/services/auth.service';
import { ActivityService } from '../../../core/services/activity.service';
import { NavbarComponent } from '../../../ui/navbar/navbar.component';
import { User } from '../../../core/models/user.model';
import { Activity } from '../../../core/models/activity.model';

@Component({
  selector: 'app-student-dashboard',
  standalone: true,
  imports: [CommonModule, RouterLink, NgChartsModule, NavbarComponent],
  templateUrl: './student-dashboard.component.html',
  styleUrls: ['./student-dashboard.component.scss']
})
export class StudentDashboardComponent implements OnInit {
  currentUser: User | null = null;
  activities: Activity[] = [];
  isLoading: boolean = true;
  
  // Activity stats
  totalActivities: number = 0;
  pendingActivities: number = 0;
  approvedActivities: number = 0;
  rejectedActivities: number = 0;
  totalPoints: number = 0;
  
  // Doughnut chart
  public doughnutChartData: ChartData<'doughnut'> = {
    labels: ['Approved', 'Pending', 'Rejected'],
    datasets: [
      {
        data: [0, 0, 0],
        backgroundColor: ['#28a745', '#ffc107', '#dc3545']
      }
    ]
  };
  
  public doughnutChartOptions: ChartConfiguration['options'] = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: 'bottom'
      }
    }
  };
  
  // Bar chart for activity types
  public barChartData: ChartData<'bar'> = {
    labels: ['Sports', 'Cultural', 'Technical', 'Professional Development', 'Community Service', 'Other'],
    datasets: [
      {
        data: [0, 0, 0, 0, 0, 0],
        label: 'Points Earned',
        backgroundColor: '#007bff'
      }
    ]
  };
  
  public barChartOptions: ChartConfiguration['options'] = {
    responsive: true,
    scales: {
      x: {
        grid: {
          display: false
        }
      },
      y: {
        beginAtZero: true
      }
    },
    plugins: {
      legend: {
        display: false
      }
    }
  };

  constructor(
    private authService: AuthService,
    private activityService: ActivityService
  ) {}

  ngOnInit(): void {
    this.currentUser = this.authService.getCurrentUser();
    this.loadActivities();
  }

  loadActivities(): void {
    this.isLoading = true;
    
    this.activityService.getMyActivities().subscribe({
      next: (response) => {
        if (response && response.data) {
          this.activities = response.data;
          this.calculateStats();
          this.updateCharts();
        } else {
          this.activities = [];
        }
        this.isLoading = false;
      },
      error: (error) => {
        console.error('Error loading activities:', error);
        this.isLoading = false;
      }
    });
  }

  calculateStats(): void {
    this.totalActivities = this.activities.length;
    this.pendingActivities = this.activities.filter(a => a.status === 'pending').length;
    this.approvedActivities = this.activities.filter(a => a.status === 'approved').length;
    this.rejectedActivities = this.activities.filter(a => a.status === 'rejected').length;
    
    this.totalPoints = this.activities
      .filter(a => a.status === 'approved')
      .reduce((sum, activity) => sum + activity.pointsAwarded, 0);
  }

  updateCharts(): void {
    // Update doughnut chart
    this.doughnutChartData.datasets[0].data = [
      this.approvedActivities,
      this.pendingActivities,
      this.rejectedActivities
    ];
    
    // Update bar chart
    const activityTypes = ['Sports', 'Cultural', 'Technical', 'Professional Development', 'Community Service', 'Other'];
    const pointsByType = activityTypes.map(type => {
      return this.activities
        .filter(a => a.status === 'approved' && a.activityType === type)
        .reduce((sum, activity) => sum + activity.pointsAwarded, 0);
    });
    
    this.barChartData.datasets[0].data = pointsByType;
  }
} 